import { writable } from 'svelte/store';
function storeValue(storage, path, value) {
    let obj = {};
    const data = storage.getItem('mmap:storage');
    if (data) {
        obj = JSON.parse(data);
    }
    const pathElements = path.split('.');
    let current = obj;
    for (let idx = 0; idx < pathElements.length; idx++) {
        const element = pathElements[idx];
        if (idx === pathElements.length - 1) {
            current[element] = value;
        }
        else {
            if (!current[element]) {
                current[element] = {};
            }
            current = current[element];
        }
    }
    storage.setItem('mmap:storage', JSON.stringify(obj));
    return obj;
}
function loadValue(storage, path, defaultValue) {
    const data = storage.getItem('mmap:storage');
    if (data) {
        const obj = JSON.parse(data);
        const pathElements = path.split('.');
        let current = obj;
        for (let idx = 0; idx < pathElements.length; idx++) {
            const element = pathElements[idx];
            if (idx === pathElements.length - 1) {
                if (current[element] !== undefined) {
                    return current[element];
                }
                else {
                    return defaultValue;
                }
            }
            else {
                if (current[element]) {
                    current = current[element];
                }
                else {
                    return defaultValue;
                }
            }
        }
    }
    return defaultValue;
}
function deleteValue(storage, path) {
    let obj = {};
    const data = storage.getItem('mmap:storage');
    if (data) {
        obj = JSON.parse(data);
    }
    const pathElements = path.split('.');
    let current = obj;
    for (let idx = 0; idx < pathElements.length; idx++) {
        const element = pathElements[idx];
        if (idx === pathElements.length - 1) {
            delete current[element];
        }
        else {
            if (!current[element]) {
                break;
            }
            current = current[element];
        }
    }
    storage.setItem('mmap:storage', JSON.stringify(obj));
    return obj;
}
function createPreferencesStore(storage) {
    const { subscribe, set } = writable(loadValue(storage, 'preferences', {}));
    return {
        subscribe,
        setPreference(path, value) {
            const updated = storeValue(storage, 'preferences.' + path, value);
            set(updated.preferences);
        }
    };
}
export const localPreferences = createPreferencesStore(localStorage);
export const sessionPreferences = createPreferencesStore(sessionStorage);
